import React, { useState } from 'react';
import styles from './timeline.module.css';
import arrow from '../../../../assets/images/arrow.svg';

const Timeline = ({ onYearChange }) => {
  const years = [
    1300, 1324, 1362, 1389, 1413, 1481, 1520, 1574, 1623, 1648, 1703, 1730, 1789, 1808, 1839, 1876,
    1918,
  ];
  const [year, setLocalYear] = useState(years[0]);

  const incrementYear = () => {
    const currentIndex = years.indexOf(year);
    if (currentIndex < years.length - 1) {
      const newYear = years[currentIndex + 1];
      setLocalYear(newYear);
      onYearChange(newYear);
    }
  };

  const decrementYear = () => {
    const currentIndex = years.indexOf(year);
    if (currentIndex > 0) {
      const newYear = years[currentIndex - 1];
      setLocalYear(newYear);
      onYearChange(newYear);
    }
  };

  return (
    <>
      <div className={styles.yearButtons}>
        <button
          className={styles.yearArrowPrev}
          onClick={decrementYear}
          disabled={year === years[0]}
        >
          <img src={arrow} alt="Previous year" />
        </button>
        <span className={styles.currentYear}>{year}</span>
        <button
          className={styles.yearArrowNext}
          onClick={incrementYear}
          disabled={year === years[years.length - 1]}
        >
          <img src={arrow} alt="Next year" />
        </button>
      </div>
      <div className={styles.timeline}>
        {years.map((y) => (
          <button
            key={y}
            className={year === y ? styles.yearButtonActive : styles.yearButton}
            onClick={() => {
              setLocalYear(y);
              onYearChange(y);
            }}
          >
            {y}
          </button>
        ))}
      </div>
    </>
  );
};

export default Timeline;
