import React from 'react';
import { useTranslation } from 'react-i18next';
import styles from './main-top.module.css';
import mainTopPic from '../../../assets/images/main-top-pic.png';

const MainTop = () => {
  const { t } = useTranslation();

  return (
    <div className={styles.parent}>
      <div className={styles.heading}>
        <p>
          {t('main-texts.main-top-header')}
          <img src={mainTopPic} alt="main-top-pic" className={styles.mainTopPic} />
        </p>
      </div>
      <div className={styles.subHeading}>
        <p>{t('main-texts.main-top-bottom')}</p>
      </div>
    </div>
  );
};

export default MainTop;
