import React, { useState } from 'react';
import styles from './culture-item.module.css'; // Update the import to a common CSS module
import { useTranslation } from 'react-i18next';

const CultureItem = ({ id, index }) => {
  const { t } = useTranslation();
  const culture = t(`countries.${id}.full-info.cultures.${index}`, { returnObjects: true });
  const [selectedImg, setSelectedImg] = useState(null);
  const [isEmpty, setIsEmpty] = useState(culture.pic3);
  const parentClass = isEmpty ? styles.parent : styles.parentEmpty;

  const handleImgClick = (imgSrc) => {
    setSelectedImg(imgSrc);
  };

  if (!culture || !culture.name || !culture.pic1) {
    return null;
  }

  return (
    <div className={parentClass}>
      <div className={index === 0 ? styles.photo_0 : styles.photo_1}>
        <img src={culture.pic1} alt="culture" />
      </div>
      <div className={index === 0 ? styles.textAndPhoto_0 : styles.textAndPhoto_1}>
        <div className={styles.texts}>
          <p className={styles.name}>{t(culture.name)}</p>
          <p className={styles.text}>
            <p>{t(`countries.city`)}</p>:<p className={styles.textBold}>{t(culture.city)}</p>
          </p>
          <p className={styles.text}>
            <p>{t(`countries.year`)}</p>:<p className={styles.textBold}> {t(culture.year)} </p>
          </p>
        </div>
        <img src={culture.pic2} alt="culture" onClick={() => handleImgClick(culture.pic2)} />
      </div>
      {culture.pic3 &&
        (<div className={styles.photos}>
            <img src={culture.pic3} alt="culture" onClick={() => handleImgClick(culture.pic3)} />
            <img src={culture.pic4} alt="culture" onClick={() => handleImgClick(culture.pic4)} />
            <img src={culture.pic5} alt="culture" onClick={() => handleImgClick(culture.pic5)} />
          </div>
        )}
      {selectedImg && (
        <div className={styles.modal} onClick={() => setSelectedImg(null)}>
          <img src={selectedImg} alt="Enlarged culture" className={styles.enlargedImage} />
        </div>
      )}
    </div>
  );
};

export default CultureItem;