import React from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';
import styles from './country-page.module.css';
import backgroundImage from '../../assets/images/country-bg-1.svg';
import Heading from '../common-components/heading/heading';
import CountryInfo from './country-page-parts/country-info';
import CountryCulture from './country-page-parts/country-culture';
import Back from '../common-components/back-arrow-button/back';

const CountryPage = () => {
  const { t } = useTranslation();
  let { id } = useParams();

  return (
    <div
      style={{
        backgroundImage: `url(${backgroundImage})`,
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
      }}
    >
      <div className="container">
        <Back />
        <div className={styles.titleBlock}>
          <Heading text={t(`countries.${id}.full-info.heading-country`)} />
          <img
            className={styles.flag}
            src={t(`countries.${id}.full-info.flagInOsman`)}
            alt="flag"
          />
        </div>
        <CountryInfo id={id} />
        <Heading text={t(`countries.${id}.full-info.heading-culture`)} />
        <CountryCulture id={id} />
      </div>
    </div>
  );
};

export default CountryPage;
