import React, { useState, useEffect } from 'react';
import styles from './main-history.module.css';
import { useTranslation } from 'react-i18next';

const MainHistory = ({ year }) => {
  const { t } = useTranslation();
  const [historyData, setHistoryData] = useState({});

  useEffect(() => {
    const dataForYear = t('texts-for-timeline', { returnObjects: true })[year.toString()];
    if (dataForYear) {
      setHistoryData(dataForYear);
    }
  }, [year, t]);

  return (
    <div className={styles.parent}>
      <div className={styles.sultanImageContainer}>
        <img className={styles.sultanImage} src={historyData['sultan-photo']} alt="sultan" />
        <img className={styles.flagImage} src={historyData['flag-photo']} alt="flag" />
        <h2>{historyData['sultan-name']}</h2>
      </div>
      <div className={styles.sultanText}>
        <p>{historyData['text-under-sultan']}</p>
      </div>
      <img className={styles.mapImage} src={historyData['map-photo']} alt="map" />
      <div className={styles.mapText}>
        <p>{historyData['text-under-map']}</p>
      </div>
    </div>
  );
};

export default MainHistory;
