import React from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import styles from './country-card.module.css';

const CountryCard = ({ id }) => {
  const { t } = useTranslation();
  const navigate = useNavigate();

  const handleClick = (e) => {
    const id = e.target.id;
    if (id) {
      navigate(`countries/${id}`);
      window.scrollTo(0, 0);
    }
  };

  return (
    <div className={styles.countryCard}>
      <div className={styles.countryInfo}>
        <p className={styles.countryName}>{t(`countries.${id}.on-map.name`)}</p>
        <img className={styles.bigFlag} src={t(`countries.${id}.on-map.flagModern`)} alt="flag" />
        <div className={styles.years}>
          <img className={styles.littleFlag} src={t(`countries.flagOsman`)} alt="flag" />
          <p className={styles.yearsText}>{t(`countries.${id}.on-map.years`)}</p>
        </div>
      </div>
      <button id={id} className={styles.button} onClick={handleClick}>
        <p id={id} className={styles.buttonText}>
          {t('countries.button-text')}
        </p>
      </button>
    </div>
  );
};

export default CountryCard;
