import React from 'react';
import styles from './main-info.module.css';
import Heading from '../../common-components/heading/heading';
import { useTranslation } from 'react-i18next';
import flag from '../../../assets/images/main-flag.png';

const MainInfo = () => {
  const { t } = useTranslation();

  return (
    <div className={styles.parent}>
      <div className={styles.left}>
        <Heading text={t('main-texts.header-history')} />
        <p>{t('main-texts.history-1')}</p>
        <p>{t('main-texts.history-2')}</p>
      </div>
      <div className={styles.right}>
        <img src={flag} alt="flag" />
        <p>{t('main-texts.history-3')}</p>
      </div>
    </div>
  );
};

export default MainInfo;
