import React from 'react';
import styles from './country-info.module.css';
import { useTranslation } from 'react-i18next';

const CountryInfo = ({ id }) => {
  const { t } = useTranslation();
  const countryPhotos = t(`countries.${id}.full-info.country-photos`, { returnObjects: true });

  return (
    <>
      <div className={styles.parent}>
        <div className={styles.left}>
          <img className={styles.littlePhoto} src={countryPhotos[0]} alt="l" />
          <img className={styles.bigPhoto} src={countryPhotos[1]} alt="a" />
          <p className={styles.text1}>{t(`countries.${id}.full-info.full-text-1`)}</p>
          <p className={styles.text2}>{t(`countries.${id}.full-info.full-text-2`)}</p>
        </div>
        <div className={styles.right}>
          <p className={styles.text3}>{t(`countries.${id}.full-info.full-text-3`)}</p>
          <img className={styles.hugePhoto} src={countryPhotos[2]} alt="a" />
          <p className={styles.text4}>{t(`countries.${id}.full-info.full-text-4`)}</p>
        </div>
      </div>
    </>
  );
};

export default CountryInfo;
