import React, { useState } from 'react';
import MainHistory from './components/main-history';
import Timeline from './components/timeline';

const MainHistoryBlock = () => {
  const [year, setYear] = useState('1300');

  return (
    <>
      {year && <MainHistory year={year} />}
      <Timeline onYearChange={setYear} />
    </>
  );
};

export default MainHistoryBlock;
